<template>
	<view class="chat-box">
		<view class="chat">
			<view class="bt-img">
				<image src="../../static/images/common/语音.png" v-if="!inVoice" @tap="changeVoice"></image>
				<image src="../../static/images/common/键盘.png" v-else @tap="changeVoice"></image>
			</view>
			<textarea class="chat-text btn" :class="inVoice?'noVoice':'isVoice'" auto-height="true" @input="inputs" v-model="msg" @tap="chooseInput"></textarea>
			<view class="voice btn" :class="inVoice?'isVoice':'noVoice'">按住录音</view>
			<view class="bt-img" @tap="emoji">
				<image src="../../static/images/common/笑脸.png"></image>
			</view>
			<view class="bt-img">
				<image src="../../static/images/common/更多+.png"></image>
			</view>
		</view>
		<view class="emojiBox" v-if="openEmoji">
			<choEmoji @chooseEm="chooseEm"></choEmoji>
		</view>
	</view>
</template>

<script>
	import choEmoji from '../emoji/emoji.vue'
	export default {
		data() {
			return {
				inVoice: false,
				openEmoji: false,
				msg:'',//输入框内容
			
			}
		},
		methods: {
			changeVoice() {
				this.inVoice = !this.inVoice
			},
			emoji() {
				this.openEmoji = !this.openEmoji
				setTimeout(()=>{
					this.getElementHeight()  //通过异步延迟获取高度
				},0)
				
			},
			// 向聊天页面发送文字
			inputs(e){
				let chatContent=e.detail.value;
				let pos=chatContent.indexOf('\n');
				if(pos!=-1&&chatContent.length>1){
					this.$emit('msgs',this.msg)
					this.msg=''
				}
			},
			getElementHeight() {
				const query = uni.createSelectorQuery().in(this);
				query.select('.chat-box').boundingClientRect(data => {					
					this.$emit('getBoxHeight',data.height)
				}).exec();
			},
			chooseEm(emoji){
				console.log(emoji);
			},
			chooseInput(){
				this.openEmoji=false;
				setTimeout(()=>{
					this.getElementHeight()  //通过异步延迟获取高度
				},0)
			}
		},
		components:{
			choEmoji
		}
	}
</script>

<style lang="scss">
	.chat-box {
		background: rgba(255, 255, 255, 0.97);
		border-top: 1px solid rgba(39, 40, 50, 0.1);
		width: 100%;
		position: fixed;
		bottom: 0;
		z-index: 100;
	
	}

	.chat {
		width: 100%;
		display: flex;
		align-items: flex-end;
		box-sizing: border-box;
		padding: 14rpx 14rpx;

		image {
			width: 56rpx;
			height: 56rpx;
			margin: 0 10rpx;
			flex: auto;
		}

		.btn {
			flex: auto;
			background-color: #F4F4F4;
			border-radius: 10rpx;
			padding: 20rpx;
			max-height: 160rpx;
		}
		
		.chat-text{
			text-align: left !important;
		}

		.isVoice {
			text-align: center;
			font-size: 24rpx;
			color: rgba(39, 40, 50, 0.6);
		}

		.noVoice {
			display: none;
		}
	}

	.emojiBox {
		width: 100%;
		height: 436rpx;
		background: rgba(236, 237, 238, 1);
		box-shadow: 0 -1rpx 0 0 rgba(0, 0, 0, 0.1);
	}
</style>
